.gallery__line-1,
.gallery__line-2,
.gallery__circle-animation {
  opacity: 0;
}

.gallery__line-1.active {
  opacity: 1;
  transition: 0.6s ease-in;
}

.gallery__line-2.active {
  opacity: 1;
  transition: 0.8s ease-in;
}

.gallery__circle-animation.active {
  opacity: 1;
  transition: 1s ease-in;
}

@keyframes fadeInOut {
  0%, 100% {
    opacity: 0.2;
  }
  50% {
    opacity: 1;
  }
}
.fade-circle {
  animation: fadeInOut 3s ease-in-out infinite;
}

.container-gallery {
  display: flex;
}

.text__gallery--320 {
